.main-title {
  font-family: 'Poppins SemiBold', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  color: '#14395E';
}

.sub-header {
  margin: 61.5px 0 18px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;

  h3 {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-size: 22px;
    line-height: 33px;
  }

  h4 {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-size: 18px;
    line-height: 27px;
  }

  button {
    max-width: 139px;
    padding: 11.5px 10px;
    font-size: 18px;
    line-height: 27px;
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    text-transform: capitalize;
    color: #1f3bb3;
    svg {
      color: #1f3bb3;
    }
  }
}

.form-wrapper {
  border: 0.5px solid #b1afaf;
  border-radius: 5px;
  width: 100%;
  padding: 33px 34px;

  h5 {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-size: 18px;
    line-height: 27px;
    color: #030303;
    margin-bottom: 7px;
  }

  .input-field {
    width: 100%;
    height: 50px;
  }

  button {
    background: #1f3bb3 0% 0% no-repeat padding-box;
    border-radius: 10px;
    color: #ffffff;
    font-size: 18px;
    line-height: 27px;
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    height: 50px;
    text-transform: capitalize;
    width: 316px;
    padding: 13px 0 12px 0;

    svg {
      color: #ffffff;
    }
  }

  button:hover {
    background: #3833df;
  }
}
